.modal_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    border-color: #000000;
    visibility: hidden;
    z-index: -1;
    opacity: 0;
    transition-property: opacity visibility;
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 1;

    &.active {
        visibility: visible;
        z-index: 10;
        opacity: 1;
    }

    .modal {
        background-color: #fff;
        color: #000;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 280px;
        height: auto;
        font-weight: 500;
        padding: 0;

        @media screen and (max-width: 1279px) {
            max-height: 90%;
            // overflow: auto;
            
        }

        @media screen and (min-width: 768px) {
            width: 704px;
            height: auto;
            margin-bottom: 20px;
            // height: 552px; 
        }

        @media screen and (min-width: 1280px) {
            // width: 50vw;
            // max-width: 90%;
            // height: 50vh;
            // max-height: 90%;
            // height: 558px;
            width: 806px;
            height: auto;
            max-height: 558px;
        }
    }
}

