.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 100;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  max-height: 100%;
  opacity: 1;
  background-color: #fff;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.4);
}

.modal__close-btn {
  background-color: inherit;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.modal__close-btn:hover > svg, .modal__close-btn:focus > svg {
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

